@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,600&display=swap&subset=latin-ext);
*{margin:0;padding:0;box-sizing:border-box}body{font-family:'Montserrat', sans-serif;background:linear-gradient(135deg, #610d67 10%, #a76262 100%);min-height:100vh}@media screen and (min-width: 800px){body{max-width:800px;margin:0 auto}}textarea:focus,input:focus,button:focus{outline:none}input[type=number]::-webkit-outer-spin-button,input[type=number]::-webkit-inner-spin-button{-webkit-appearance:none;margin:0}input[type=number]{-moz-appearance:textfield}

